export default {
	data() {
		return {
			text:"",
			logo:"",
			logos:"",
			w:"",
			m:"",
			lw:"",
			makeCode:"",
			baseCode:""
		}
	},
	methods: {
		
		selectLogo(){
			uni.chooseImage({
			    count: 1, //默认9
			    sizeType: ['compressed'], //压缩图，
			    sourceType: ['album'], //从相册选择
			    success: (res)=> {
					this.logo="data:image/jpeg;base64,"+wx.getFileSystemManager().readFileSync(res.tempFilePaths[0], "base64")
					 this.logos=encodeURI(wx.getFileSystemManager().readFileSync(res.tempFilePaths[0], "base64"))
			    },
				
			});
		},
		getCode(){
			this.$minApi.getCode({
				text:this.text,
				bgcolor:"ffffff",
				fgcolor:"000000",
				logo:this.logos,
				w:this.w,
				m:this.m,
				lw:"30",
				type:"1",
				key: "e7a5d06a73fdf487dc5b54f7bda2fb82"
			}).then(data => {
				console.log(data)
				// this.makeCode=data.data.data
				this.baseCode=data.data.result.base64_image
				this.makeCode="data:image/jpeg;base64,"+this.baseCode
			})
		},
		// 长按保存图片
		  saveImg(e){
			  console.log(e)
		    let url = this.baseCode;
			
		    //用户需要授权
		    wx.getSetting({
		      success: (res) => {
		        if (!res.authSetting['scope.writePhotosAlbum']) {
		          wx.authorize({
		            scope: 'scope.writePhotosAlbum',
		            success:()=> {
		              // 同意授权
		              this.saveImg1(url);
		            },
		            fail: (res) =>{
		              console.log(res);
		            }
		          })
		        }else{
		          // 已经授权了
		          this.saveImg1(url);
		        }
		      },
		      fail: (res) =>{
		        console.log(res);
		      }
		    })   
		  },
		  
		  saveImg1(url){
			  var save = wx.getFileSystemManager();
			  var number = Math.random();
			   var imgSrc =  this.baseCode;//base64编码
		   save.writeFile({
		         filePath: wx.env.USER_DATA_PATH + '/pic' + number + '.png',
		         data: imgSrc,
		         encoding: 'base64',
		         success: res => {
		           wx.saveImageToPhotosAlbum({
		             filePath: wx.env.USER_DATA_PATH + '/pic' + number + '.png',
		             success: function (res) {
		               wx.showToast({
		                 title: '保存成功',
		               })
		             },
		             fail: function (err) {
		               console.log(err)
		             }
		           })
		           console.log(res)
		         }, fail: err => {
		           console.log(err)
		         }
		       })
		  },
	},
	created() {
	},
	onLoad() {
		
	}
}
